@use './theme/colors';
@use './theme/fonts';
@use './_header';
@use './_footer';
@use './_cards';

@import '/node_modules/tailwindcss/base.css';
@import '/node_modules/tailwindcss/components.css';
@import '/node_modules/tailwindcss/utilities.css';

body {
  background-color: colors.$cream;
}

.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal !important;
}

a, select, button, input, textarea, img, li{
  &:focus{
  outline-color:colors.$white;
  outline-width: .1rem;
  border: .25rem solid colors.$yellow;
  opacity: 0.9;
  }
}


.label--hidden {
  position: absolute;
  left: -9999px; //lol 
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

select{
	appearance: none; 
	-webkit-appearance: none;
	-moz-appearance: none; 
}

.select--btn {
  background-color: colors.$white;
  color: colors.$black;
  font-size: fonts.$font-size--min;
  font-family: fonts.$p;
  font-weight: fonts.$ultra;
  border: .25rem solid colors.$black;

  &:hover {
    color: colors.$white;
    background-color: colors.$black;
  }

  &:focus {
    outline-color: colors.$white;
    outline-width: .25rem;
    border: .25rem solid colors.$yellow;
    opacity: 0.9;
  }
}

button.form--btn {
  font-family: fonts.$tonic;
  font-size: fonts.$font-size;
  color: colors.$white;
  background-color: colors.$black;
  border: 0 solid;
  display: flex;
  place-items: center;

  &:hover {
    color: colors.$black;
    background-color: colors.$white;
    border: .25rem solid colors.$black;
  }
  
  &:focus {
    outline-color: colors.$white;
    outline-width: .25rem;
    border: .25rem solid colors.$yellow;
    opacity: 0.9;
  }
}

.streaming a img:hover {
  filter: invert(41%) sepia(58%) saturate(1039%) hue-rotate(324deg) brightness(78%) contrast(110%);
}

.title__sub {
  font-family: fonts.$sub;
}

.pic__shadow{
  box-shadow: 10px 10px rgba(0, 0, 0, 1);

  &--video{
    width: 100%;
    height: 100%;
  }
}

.container-fluid {

  width: 100%;
 
  padding-right: 15px;
 
  padding-left: 15px;
 
  margin-right: auto;
 
  margin-left: auto;
 
 }

 .text__hover--hidden{
  display: none;
 }
